import { Component } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';

@Component({
    selector: 'atlas-ag-grid-button',
    templateUrl: './ag-grid-button.component.html',
    styleUrls: ['./ag-grid-button.component.scss'],
})
export class AgGridButtonComponent implements ICellRendererAngularComp {
    disabled: boolean;
    public params: any;

    constructor() { }

    ngOnInit() {
    }

    agInit(params: any): void {
        this.params = params;
        this.disabled = false;
        this.disabled = this.params.disabled;
    }

    refresh(params: any): boolean {
        return false;
    }

    onMessageButtonClicked() {
        if (this.params.data.message) {
            this.params.context.componentParent.methodFromParent(this.params.data.message);
        } else if (this.params.data.error) {
            this.params.context.componentParent.methodFromParent(this.params.data.error);
        }
    }

}
